body {
    font-family: 'Comfortaa', cursive;
    box-sizing: border-box;
    background-image:  linear-gradient(to right, #141e30, #243b55);
    background-repeat: no-repeat;
    background-size: cover;
}

.container {
    max-width: 1024px;
    margin: 0 auto;
}

a {
    text-decoration: none;
}

/* navbar */
.navbar {
    height: 3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #172636;
}

.navbar a {
    color: brown;
}
/* navbar */

.home_btn {
    display: flex;
    justify-content: center;
    align-items: center;
    height: -webkit-fill-available;
}

.home_btn .btn {
    width: 20rem;
    background-color: rgb(236, 108, 108);
    color: #f8f5f5;
    text-align: center;
    font-size: 2rem;
    padding: 2rem 1rem;
    transition: all .2s ease-out;
}

.home_btn .btn:hover {
    box-shadow: 0 0px 50px 0px rgba(255, 255, 255, 1);
    border-radius: 10px;
    transform: scale(.9);
}

.image-main {
    max-width: 1024px;
    height: 90vh;
    display: flex;
    padding: 2rem 1rem 0 1rem;
    color: #f8f5f5;
 
}

.nasa-image {
    height: 70vh;
    width: 30%;
}

.nasa-image img {
    width: 100%;
    height: 100%;
}

.content {
    width: 70%;
    padding: 0 1rem 0 2rem;
}

.content .title {
    font-size: 2.2rem;
    color: brown;
}
.content .date {
    font-size: 1.2rem;
    color: brown;
    margin: 10px 0;
}
.content .explanation {
    font-size: 1.1rem;
    line-height: 1.5;
}